@import (less) 'color';

.page-steps {
    list-style: none;
    padding: 0;
    margin: 0;
    height: 2em;
    li {
        display: inline-block;
        height: 2em;
        line-height: 2em;
        .row {
            line-height: 0em;
        }
    }
    .page-step {
      .col {
        display: inline-table;
      }
      .row {
        display: table-cell;
      }
    }
    .page-steps-number {
        display: inline-block;
        border: 1px solid @gray;
        font-weight: bold;
        color: @gray;
        text-align: center;
        -webkit-border-radius: 50%;
        -moz-border-radius: 50%;
        border-radius: 50%;
        width: 2em;
        height: 2em;
        font-size: 0.785em;
        line-height: 2em;
        margin: 0 0.125em 0 0.125em;
    }
    .page-steps-number.active {
        color: @green;
        border: 1px solid @green;
    }
    .page-steps-number.done {
        color: @white;
        background-color: @green;
        border-color: @green;
    }
    .page-step-label {
        font-weight: normal;
        color: @green;
        font-size: 0.6875em;
        line-height: 0.5em;
        text-transform: uppercase;
        margin-left: 0.5em;
        margin-top: 0.4em;
    }
    .page-steps-label {
        font-weight: bold;
        color: @green;
        font-size: 0.785em;
        line-height: 1em;
        text-transform: uppercase;
        margin-left: 0.5em;
    }
}
.page-steps-container {
    background-color: @white;
}
@media (min-width:768px) {
    .page-steps {
        .page-steps-label {
            font-size: 0.938em;
        }
        .page-steps-number {
            font-size: 0.938em;
        }
    }
}
@-moz-document url-prefix() {
    .page-steps {
      .page-step {
        .col {
          display: inline-flex;
        }
      }
    }
}
@media screen and (-ms-high-contrast: active), (-ms-high-contrast: none) {
  .page-steps {
    .page-step {
      .col {
        .row {
          top: -10px;
        }
      }
    }
  }
}

.contrasto-on {
    .page-steps {
        .page-steps-number {
            color: @black;
        }
        .page-steps-number.active {
            color: @black;
            border: 1px solid @black;
        }
        .page-steps-number.done {
            color: @white;
            background-color: @black;
            border-color: @black;
        }
        .page-step-label {
            color: @black;
        }
        .page-steps-label {
            color: @black;
        }
    }
    .page-steps-container {
        background-color: @white;
    }
}